import React, { Component } from "react";
import "./App.css";
import Accordion from "react-bootstrap/lib/Accordion";
import Panel from "react-bootstrap/lib/Panel";
import ButtonToolbar from "react-bootstrap/lib/ButtonToolbar";
import Button from "react-bootstrap/lib/Button";
import Modal from "react-bootstrap/lib/Modal";
import FormGroup from "react-bootstrap/lib/FormGroup";
import ControlLabel from "react-bootstrap/lib/ControlLabel";
import FormControl from "react-bootstrap/lib/FormControl";

class App extends Component {
  state = {
    recipes: [

    ],
    showAdd: false,
    showEdit: false,
    currentIndex: 0,
    newRecipe: { recipeName: "", ingredients: [] }
  };

  //Deletes a recipe
  deleteRecipe(index) {
    let recipes = this.state.recipes.slice();
    recipes.splice(index, 1);
    localStorage.setItem("recipes", JSON.stringify(recipes));
    this.setState({ recipes });}
  //Updates newest recipe
  updateNewRecipe(recipeName, ingredients) {
    this.setState({
      newRecipe: { recipeName: recipeName, ingredients: ingredients }
    });}
  //Update recipe name
  updateRecipeName(recipeName, currentIndex) {
    let recipes = this.state.recipes.slice();
    recipes[currentIndex] = {
      recipeName: recipeName,
      ingredients: recipes[currentIndex].ingredients
    };
    localStorage.setItem("recipes", JSON.stringify(recipes));
    this.setState({ recipes });}
  //Update ingredients
  updateIngredients(ingredients, currentIndex) {
    let recipes = this.state.recipes.slice();
    recipes[currentIndex] = {
      recipeName: recipes[currentIndex].recipeName,
      ingredients: ingredients
    };
    localStorage.setItem("recipes", JSON.stringify(recipes));
    this.setState({ recipes });}
  //Save a new recipe to the database of recipes
  saveNewRecipe(newRecipe) {
    let recipes = this.state.recipes.slice();
    recipes.push(newRecipe);
    localStorage.setItem("recipes", JSON.stringify(recipes));
    this.setState({ recipes });
    this.setState({ newRecipe: { recipeName: 0, ingredients: [] } });
    this.close();}

  //After the application runs, run this code
  componentDidMount() {
    let recipes = JSON.parse(localStorage.getItem('recipes')) || [];
    this.setState({recipes});
  }

  //Closes a Modal
  close = () => {
    if (this.state.showAdd) {
      this.setState({ showAdd: false });
    } else if (this.state.showEdit) {
      this.setState({ showEdit: false });
    }
  };
  //Opens a Modal
  open = (state, currentIndex) => {
    this.setState({ [state]: true });
    this.setState({ currentIndex });
  };

  render() {
    var { recipes, newRecipe, currentIndex } = this.state;

    return (
      <div>
        <div className="logo"><b>recipe box</b></div>
        <div className="copyright">coded by Rafael Pessoa</div>
        <Button
          bsStyle="success"
          onClick={event => this.open("showAdd", currentIndex)}
        >
          Add new recipe
        </Button>

        {(recipes.length>0 &&
          <div className="table">
            <Accordion>
              {recipes.map((recipe, index) => (
                <Panel eventKey={index} key={index} bsStyle="danger">
                  <Panel.Heading>
                    <Panel.Title toggle> {recipe.recipeName} </Panel.Title>
                  </Panel.Heading>
                  <Panel.Body collapsible>
                    <div id="ingredientsTitle">
                      <b>Ingredients</b>
                    </div>

                    {recipe.ingredients.map(item => <p key={item}>{item}</p>)}

                    <ButtonToolbar className="ButtonToolbar">
                      <Button
                        bsStyle="danger"
                        bsSize="small"
                        onClick={event => this.deleteRecipe(index)}
                      >
                        Delete
                      </Button>
                      <Button
                        bsStyle="primary"
                        bsSize="small"
                        onClick={event => this.open("showEdit", index)}
                      >
                        Edit
                      </Button>
                    </ButtonToolbar>
                  </Panel.Body>
                </Panel>
              ))}
            </Accordion>
          </div>
          )}
        
        
        <Modal show={this.state.showAdd} onHide={this.close}>
          <Modal.Header closeButton>
            <Modal.Title>Add new recipe</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <FormGroup controlId="formBasicText">
              <ControlLabel>Recipe Name</ControlLabel>
              <FormControl
                type="text"
                value={newRecipe.recipeName}
                placeholder="Recipe name (Spaghetti & Meatballs)"
                onChange={event =>
                  this.updateNewRecipe(
                    event.target.value,
                    newRecipe.ingredients
                  )
                }
              />
            </FormGroup>
            <FormGroup controlId="formControlIsTextarea">
              <ControlLabel>Ingredients</ControlLabel>
              <FormControl
                type="textarea"
                value={newRecipe.ingredients}
                placeholder="Ingredients separated by commas (Tomato, Meatballs, Pasta)"
                onChange={event =>
                  this.updateNewRecipe(
                    newRecipe.recipeName,
                    event.target.value.split(",")
                  )
                }
              />
            </FormGroup>
          </Modal.Body>
          <Modal.Footer>
            <Button
              bsStyle="success"
              onClick={event => this.saveNewRecipe(newRecipe)}
            >
              Add the recipe
            </Button>
          </Modal.Footer>
        </Modal>

        {(recipes.length>0 &&
        <Modal show={this.state.showEdit} onHide={this.close}>
          <Modal.Header closeButton>
            <Modal.Title>Edit recipe</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <FormGroup controlId="formBasicText">
              <ControlLabel>Recipe Name</ControlLabel>
              <FormControl
                type="text"
                value={recipes[currentIndex].recipeName}
                placeholder="Recipe name (Spaghetti & Meatballs)"
                onChange={event =>
                  this.updateRecipeName(event.target.value, currentIndex)
                }
              />
            </FormGroup>
            <FormGroup controlId="formControlIsTextarea">
              <ControlLabel>Ingredients</ControlLabel>
              <FormControl
                componentClass="textarea"
                value={recipes[currentIndex].ingredients}
                placeholder="Ingredients separated by commas (Tomato, Meatballs, Pasta)"
                onChange={event =>
                  this.updateIngredients(
                    event.target.value.split(","),
                    currentIndex
                  )
                }
              />
            </FormGroup>
          </Modal.Body>
          <Modal.Footer>
            <Button onClick={this.close}>Close</Button>
          </Modal.Footer>
        </Modal>
         )}
        
        
      </div>
    );
  }
}

export default App;


